.main {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--blueB);
}

.container {
    margin: 0 auto;
    padding: 40px 15px;
    min-width: 1200px;
}

.content {
    background: var(--white);

    padding: 40px 0;
    border: 1px solid var(--grey);
    border-radius: 40px;

    overflow: hidden;
}

.list {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 40px;
    text-align: center;
}

.list__item {
    font-size: 18px;
    line-height: 1.5;
}

.list__item_title {
    font-size: 32px;
    font-weight: 700;
    color: var(--title-1);
    text-align: center;
}

.list__item_text {
    margin-top: 0.5em;
}
